import React, {useState} from 'react';
import { IonContent, IonPage, IonImg, IonButton } from '@ionic/react';
import BusinessCard from '../components/BusinessCard';

import { Plugins } from '@capacitor/core';
const { Storage } = Plugins;

const Profile: React.FC = (props) => {
  const [user, setUser] = useState({id: '', name: '', email: '', avatar: '', cover: '', card: {}});

    async function loadData() {
        const { value } = await Storage.get({ key: 'user' });
        let data: any;

        if(value){
            data = JSON.parse(value);
            setUser(data);

            var jwt = require('jwt-decode');
            let tokenDecode = jwt(data.token);

            if(tokenDecode.exp < Math.floor(Date.now() / 1000)){
              await Storage.remove({ key: 'user' });
              (props as any).history.push('/login');
              window.location.reload(true);
            }
        }
    }

    if(!user.name){
        loadData();
    }

  function goForward(){
    (props as any).history.push({pathname: '/edit-profile', state: {user: user, settingsPage: false}});
  }

  return (
    <IonPage>
      <IonContent>
        <div>
            <div className="profile-header">
              <IonButton className="profile-edit" fill="clear" onClick={() => goForward()}>Edit</IonButton>
              <IonImg class="profile-large-image" src={user.cover}/>
              <IonImg class="profile-small-image" src={user.avatar} />
            </div>
            {user.card && 
              <div className="profile-content">
                <BusinessCard {...user.card} />
              </div>
            }
        </div>
      </IonContent>
    </IonPage>
  );
};

export default Profile;
