import React, {useState} from 'react';
import { IonContent, IonToolbar, IonHeader, IonPage, IonButton, IonButtons, IonBackButton } from '@ionic/react';
import BusinessCard from '../components/BusinessCard';

const SingleContact: React.FC = (props: any) => {
  const [contact, setContact] = useState({} as any);

  if(!contact.name){
    setContact(props.history.location.state.contact);
  }

  return (
    <IonPage>
      <IonHeader className="single-contact-header">
        <IonToolbar>
            <IonButtons slot="start">
                <IonButton>
                    <IonBackButton text="Back" mode="ios"></IonBackButton>
                </IonButton>
            </IonButtons>
          </IonToolbar>
      </IonHeader>
      <IonContent>
        <div className="container single-contact-container">
            <BusinessCard {...contact} />
        </div>
      </IonContent>
    </IonPage>
  );
};

export default SingleContact;
