import React, {useState } from 'react';
import { IonContent, IonPage, IonButton, IonInput, IonToast, IonLoading } from '@ionic/react';

import { Plugins } from '@capacitor/core';
const { Storage } = Plugins;

const Login: React.FC = (props) => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const [showToastMessage, setShowToast] = useState(false);
    const [showLoading, setShowLoading] = useState(false);
    const [toastMessage, setToastMessage] = useState('');

    async function login(){
        let url = 'api/v1/login';
        let validationErrorMessage = '';
        let credentials = {
          email: email,
          password: password
        }

        setToastMessage('');
        setShowToast(false);

        if(email === ''){
          validationErrorMessage += 'An email address is required.<br/>';
        }

        if(password === ''){
          validationErrorMessage += 'A password is required.<br/>';
        }

        if(validationErrorMessage !== ''){
          setToastMessage(validationErrorMessage);
          setShowToast(true);
          return false;
        }
    
        setShowLoading(true);

        //Post the login attempt...
        let response = await fetch(
            url,{
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              'Accept': 'application/json'
            },
            body: JSON.stringify(credentials)
          }
        );
          
        let data = await response.json();

        setShowLoading(false);

        if(data.error)
        {
            let errorData = '';

            errorData += data.error + '<br/>';

            setToastMessage(errorData);
            setShowToast(true);
        }
        else
        {
            await Storage.set({
                key: 'user',
                value: JSON.stringify({
                id: data.user.id,
                name: data.user.name,
                email: data.user.email,
                card: data.user.card,
                cover: data.user.cover,
                avatar: data.user.avatar,
                token: data.token
                })
            });

            setEmail('');
            setPassword('');

            (props as any).history.push('/profile');
        }
    }

    function handleNewAccount(){
        (props as any).history.push('/create-account')
    }

    function handleForgotPassword(){
        (props as any).history.push('/forgot-password')
    }

  return (
    <IonPage>
      <IonContent >
        <div slot="fixed" className="login-content">
            <div className="login-form">
                <div>
                    <img id="app-logo" src="/assets/imgs/logo.png" alt="Follow App" />
                    <IonInput value={email} placeholder="Email" onIonChange={e => setEmail(e.detail.value!)}></IonInput>
                    <IonInput value={password} placeholder="Password" type="password" onIonChange={e => setPassword(e.detail.value!)}></IonInput>
                    <IonButton onClick={() => login()} className="login" fill="clear" expand="full">
                        Login
                    </IonButton>
                    <IonButton onClick={() => handleForgotPassword()} fill="clear" expand="full">
                        Forgot Password
                    </IonButton>
                </div>
            </div>
            <div className="bottom-button">
                <IonButton onClick={() => handleNewAccount()} fill="clear">
                    Don't have an account? Sign Up
                </IonButton>
            </div>
        </div>
        <IonLoading
          isOpen={showLoading}
          onDidDismiss={() => setShowLoading(false)}
          message={'Please wait...'}
          duration={5000}
        />
        <IonToast
          isOpen={showToastMessage}
          onDidDismiss={() => setShowToast(false)}
          message= {toastMessage}
          duration={5000}
        />
      </IonContent>
    </IonPage>
  );
};

export default Login;
