import React, {useState } from 'react';
import { IonImg, IonButton, IonLabel, IonInput, IonSelect, IonSelectOption, IonIcon, IonToast, IonLoading } from '@ionic/react';
import { close } from 'ionicons/icons';

import { Plugins } from '@capacitor/core';
const { Storage } = Plugins;

interface ContainerProps {
    user: any;
}

const ProfileForm: React.FC<ContainerProps> = (user: any) => {
    const [industries, setIndustries] = useState([] as any);
    const [name, setName] = useState(user.name);
    const [email, setEmail] = useState(user.email);
    const [company, setCompany] = useState(user.card ? user.card.company : '');
    const [industry, setIndustry] = useState(user.card ? user.card.industry_id : '');
    const [industryText, setIndustryText] = useState(user.card ? user.card.industry : '');
    const [title, setTitle] = useState(user.card ? user.card.employment_title : '');
    const [address, setAddress] = useState(user.card ? user.card.address : '');
    const [city, setCity] = useState(user.card ? user.card.city : '');
    const [state, setState] = useState(user.card ? user.card.state : '');
    const [zip, setZip] = useState(user.card ? user.card.zip : '');
    const [phone, setPhone] = useState(user.card ? user.card.phone : '');
    const [website, setWebsite] = useState(user.card ? user.card.website : '');
    const [linkedin, setLinkedin] = useState(user.card ? user.card.linkedin : '');
    const [preferred, setPreferred] = useState(user.preferred_contact ? user.preferred_contact : 'NONE');

    const [showToastMessage, setShowToast] = useState(false);
    const [showLoading, setShowLoading] = useState(false);
    const [toastMessage, setToastMessage] = useState('');

    async function updateProfile(){
        let url = '';
        let action = '';
        let validationErrorMessage = '';
        let payload = {};

        var jwt = require('jwt-decode');
        let tokenDecode = jwt(user.token);

        if(tokenDecode.exp < Math.floor(Date.now() / 1000))
        {
            await Storage.remove({ key: 'user' });
            window.location.href = '/login'
            window.location.reload(true);
        }

        //Update the user first...
        url = 'api/v1/users/' + user.id;
        action = 'PATCH';

        if(preferred !== '')
        {
            payload = {
                id: user.id,
                name: name,
                preferred_contact: preferred
            }
        }
        else
        {
            payload = {
                id: user.id,
                name: name
            }
        }

        setToastMessage('');
        setShowToast(false);

        if(name === ''){
          validationErrorMessage += 'Your name is required.<br/>';
        }

        if(validationErrorMessage !== ''){
          setToastMessage(validationErrorMessage);
          setShowToast(true);
          return false;
        }

        setShowLoading(true);

        //Post the update attempt...
        let response = await fetch(
            url,{
            method: action,
            headers: {
              'Content-Type': 'application/json',
              'Accept': 'application/json',
              'Authorization' : 'Bearer ' + user.token
            },
            body: JSON.stringify(payload)
          }
        );
          
        let data = await response.json();

        setShowLoading(false);

        if(data.errors)
        {
            let errorData = '';

            Object.keys(data.errors).forEach(key => {
                errorData += data.errors[key] + '<br/>';
            })

            setToastMessage(errorData);
            setShowToast(true);
            return false;
        }

        //Update the card...
        if(!user.card){
            url = 'api/v1/user/cards';
            action = 'POST';
        }
        else {
            url = 'api/v1/user/cards/' + user.id;
            action = 'PATCH';
        }

        validationErrorMessage = '';
        payload = {
            company: company,
            address: address,
            city: city,
            state: state,
            zip: zip,
            phone: phone,
            user_id: user.id,
            industry: industry,
            employment_title: title,
            website: website,
            linkedin_path: linkedin
        }

        //Post the update attempt...
        response = await fetch(
            url,{
            method: action,
            headers: {
              'Content-Type': 'application/json',
              'Accept': 'application/json',
              'Authorization' : 'Bearer ' + user.token
            },
            body: JSON.stringify(payload)
          }
        );
          
        data = await response.json();

        setShowLoading(false);

        if(data.errors)
        {
            let errorData = '';

            Object.keys(data.errors).forEach(key => {
                errorData += data.errors[key] + '<br/>';
            })

            setToastMessage(errorData);
            setShowToast(true);
        }
        else
        {
            if(action === 'POST'){
                await Storage.set({
                    key: 'user',
                    value: JSON.stringify({
                        id: user.id,
                        name: user.name,
                        email: user.email,
                        card: JSON.stringify(data.card),
                        cover: user.cover,
                        avatar: user.avatar,
                        token: user.token
                    })
                });
            }
            else {
                await Storage.set({
                    key: 'user',
                    value: JSON.stringify({
                        id: data.id,
                        name: data.name,
                        email: data.email,
                        card: data.card,
                        cover: data.cover,
                        avatar: data.avatar,
                        token: user.token,
                        preferred_contact: data.preferred_contact
                    })
                });
            }

            setToastMessage('Your profile has been updated!');
            setShowToast(true);
        }
    }

    async function loadIndustries(){
        var jwt = require('jwt-decode');
        let tokenDecode = jwt(user.token);

        if(tokenDecode.exp < Math.floor(Date.now() / 1000)){
          await Storage.remove({ key: 'user' });
          window.location.href = '/login';
          window.location.reload(true);
        }

        let url = 'api/v1/industries';
        let response:any = await fetch(
            url,{
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Authorization' : 'Bearer ' + user.token
                }
            }
        );
            
        let data = await response.json();
        setIndustries(data);
    }

    if(!industries.length){
        loadIndustries();
    }

    function setSelectedText(id:any){
        industries.some(function (value:any, index:any, _arr:any) {
            if(value.id === id) {
                setIndustry(id);
                setIndustryText(value.name);
            }

            return value.id === id;
        });
    }

    function loadIndustryList(industryList: []){
        // Create an empty array that will hold the final JSX output.
        let buffer:any = [];

        if(industryList) { 
            industryList.forEach( (key: any) => {
                buffer.push(<IonSelectOption key={key.id} value={key.id}>{key.name}</IonSelectOption> )
            })
        }

        // And return the buffer for display inside the render() function
        return (
            <IonSelect value={industry} selectedText={industryText} placeholder="Select Industry" onIonChange={e => {setSelectedText(e.detail.value!);}}>
                {buffer}
            </IonSelect>
        );
    }

    return (
        <div>
            <div className="profile-header">
                <IonButton className="profile-edit" color="light" fill="clear">Edit</IonButton>
                  <IonButton className="modal-close" color="light" fill="clear" routerLink="/profile">
                    <IonIcon icon={close}></IonIcon>
                  </IonButton>
                <IonImg class="profile-large-image" src={user.cover} />
                <IonImg class="profile-small-image" src={user.avatar} />
            </div>
            <div className="profile-content">
                <IonLabel>Name</IonLabel>
                <IonInput value={name} placeholder="Name" onIonChange={e => setName(e.detail.value!)}></IonInput>
                <IonLabel>Email</IonLabel>
                <IonInput readonly value={email} placeholder="Email Address" onIonChange={e => setEmail(e.detail.value!)}></IonInput>
                <IonLabel>Company</IonLabel>
                <IonInput value={company} placeholder="Company Name" onIonChange={e => setCompany(e.detail.value!)}></IonInput>
                <IonLabel>Industry</IonLabel>
                {
                    loadIndustryList(industries)
                }
                <IonLabel>Employment Title</IonLabel>
                <IonInput value={title} placeholder="Title" onIonChange={e => setTitle(e.detail.value!)}></IonInput>
                <IonLabel>Address</IonLabel>
                <IonInput value={address} placeholder="Address" onIonChange={e => setAddress(e.detail.value!)}></IonInput>
                <IonLabel>City</IonLabel>
                <IonInput value={city} placeholder="City" onIonChange={e => setCity(e.detail.value!)}></IonInput>
                <IonLabel>State</IonLabel>
                <IonInput value={state} placeholder="State" onIonChange={e => setState(e.detail.value!)}></IonInput>
                <IonLabel>Zip Code</IonLabel>
                <IonInput value={zip} placeholder="Zip Code" onIonChange={e => setZip(e.detail.value!)}></IonInput>
                <IonLabel>Phone</IonLabel>
                <IonInput value={phone} placeholder="Phone Number" onIonChange={e => setPhone(e.detail.value!)}></IonInput>
                <IonLabel>Website</IonLabel>
                <IonInput value={website} placeholder="Website" onIonChange={e => setWebsite(e.detail.value!)}></IonInput>
                <IonLabel>LinkedIn Username</IonLabel>
                <IonInput value={linkedin} placeholder="Linkedin Username" onIonChange={e => setLinkedin(e.detail.value!)}></IonInput>
                <IonLabel>Preferred Method</IonLabel>
                <IonSelect value={preferred} placeholder="Select Contact Method" onIonChange={e => setPreferred(e.detail.value)}>
                    <IonSelectOption value="none">
                        None
                    </IonSelectOption>
                    <IonSelectOption value="email">
                        Email
                    </IonSelectOption>
                    <IonSelectOption value="phone">
                        Phone
                    </IonSelectOption>
                    <IonSelectOption value="mail">
                        Mail
                    </IonSelectOption>
                </IonSelect>
                <IonButton onClick={() => updateProfile()} expand="full">Update</IonButton>
            </div>
            <IonLoading
            isOpen={showLoading}
            onDidDismiss={() => setShowLoading(false)}
            message={'Please wait...'}
            duration={5000}
            />
            <IonToast
            isOpen={showToastMessage}
            onDidDismiss={() => setShowToast(false)}
            message= {toastMessage}
            duration={5000}
            />
        </div>
    );
};

export default ProfileForm;
