import React from 'react';

const BusinessCard: React.FC = (contact: any) => {
    return (
        <div className="contact-business-card">
            <h2>{contact.name}</h2>
            <h3>{contact.industry}</h3>
            <div className="contact-business-header">
                <h3>Business Card Info</h3>
            </div>
            <div className="contact-business-details">
                <div>
                    <div>Company:</div>
                    <div>{contact.company}</div>
                </div>
                <div>
                    <div>Employment Title:</div>
                    <div>{contact.employment_title}</div>
                </div>
                {contact.address && 
                    <div>
                        <div>Address:</div>
                        <div>{contact.address}<br/>{contact.city}, {contact.state} {contact.zip}</div>
                    </div>
                }
                {contact.website && 
                    <div>
                        <div>Website:</div>
                        <div>{contact.website}</div>
                    </div>
                }
            </div>
        </div>
    );
};

export default BusinessCard;