import React, { useState } from 'react';
import { IonContent, IonPage, IonList, IonItemDivider, IonItem, IonToggle, IonLabel, IonLoading, IonToast } from '@ionic/react';

import { Plugins } from '@capacitor/core';
const { Storage } = Plugins;

const Settings: React.FC = (props) => {
    const [user, setUser] = useState({id: '', name: '', email: '', avatar: ''});

    const [showToastMessage, setShowToast] = useState(false);
    const [showLoading, setShowLoading] = useState(false);
    const [toastMessage] = useState('');

    async function loadData() {
        const { value } = await Storage.get({ key: 'user' });
        let data: any;

        if(value){
            data = JSON.parse(value);
            setUser(data);
        }
    }

    if(!user.name){
        loadData();
    }

    function setSettings(type: string){
      if(type === 'notification'){
        //TODO: Add API call for notification
      } else if(type === 'email'){
        //TODO: Add API call for email
      } else {
        //TODO: Add API call for report crashes
      }
    }

    function goForward(){
      (props as any).history.push({pathname: '/edit-profile', state: {user: user, settingsPage: true}});
    }

    async function logout(){
        await Storage.remove({ key: 'user' });
        (props as any).history.push('/login');
        window.location.reload(true);
    }

  return (
    <IonPage>
      <IonContent>
        <div className="settings-header">
            <h1>Settings</h1>
        </div>
        <div className="settings-name">
            <div>
                <img alt={user.name} src={user.avatar} />
            </div>
            <div>
                 <p className="name">{user.name}</p>
                <p>{user.email}</p>
            </div>
        </div>
        <IonList className="settings-list">
            <IonItemDivider>
                Social
            </IonItemDivider>
            <IonItem button onClick={() => goForward()} >
                Edit Profile
            </IonItem>
            <IonItem button onClick={() => {logout()}} >
                Log Out
            </IonItem>
            <IonItemDivider>
                Communications
            </IonItemDivider>
            <IonItem>
                <IonToggle onIonChange={() => setSettings('notification')} mode="ios" color="success" checked slot="end"></IonToggle>
                <IonLabel>Notifications</IonLabel>
            </IonItem>
            <IonItem>
                <IonToggle onIonChange={() => setSettings('email')} mode="ios" color="success" checked slot="end"></IonToggle>
                <IonLabel>Email</IonLabel>
            </IonItem>
            <IonItem>
                <IonToggle onIonChange={() => setSettings('report')} mode="ios" color="success" checked slot="end"></IonToggle>
                <IonLabel>Report Crashes</IonLabel>
            </IonItem>
        </IonList>
        <IonLoading
          isOpen={showLoading}
          onDidDismiss={() => setShowLoading(false)}
          message={'Please wait...'}
          duration={5000}
        />
        <IonToast
          isOpen={showToastMessage}
          onDidDismiss={() => setShowToast(false)}
          message= {toastMessage}
          duration={5000}
        />
      </IonContent>
    </IonPage>
  );
};

export default Settings;
