import React, {useState } from 'react';
import { IonContent, IonPage, IonButton, IonInput, IonToast, IonLoading } from '@ionic/react';

const ForgotPassword: React.FC = (props) => {
    const [email, setEmail] = useState('');

    const [showToastMessage, setShowToast] = useState(false);
    const [showLoading, setShowLoading] = useState(false);
    const [toastMessage, setToastMessage] = useState('');

    function returnToLogin(){
        (props as any).history.push('/login')
    }

    async function handleResetPassword(){
        let url = 'api/v1/password/reset';
        let payload = {
          email: email
        }

        if(email === ''){
            setToastMessage('An email address is required.');
            setShowToast(true);
            return false;
        }

        setShowLoading(true);

        let response = await fetch(
            url,{
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              'Accept': 'application/json'
            },
            body: JSON.stringify(payload)
          }
        );

        let data = await response.json();

        setShowLoading(false);

        if(data.errors) {
            let errorData = '';
  
            Object.keys(data.errors).forEach(key => {
              errorData += data.errors[key] + '<br/>';
            })
  
            setToastMessage(errorData);
            setShowToast(true);
        } else {
          let successMessage = 'Password recovery instructions have been sent to: ' + email;
          setToastMessage(successMessage);
          setShowToast(true);
        }
    }

  return (
    <IonPage>
      <IonContent >
        <div slot="fixed" className="login-content">
            <div className="login-form">
                <div>
                    <IonInput value={email} placeholder="Email" onIonChange={e => setEmail(e.detail.value!)}></IonInput>
                    <IonButton className="reset-password" onClick={() => handleResetPassword()} fill="clear" expand="full">
                        Reset Password
                    </IonButton>
                </div>
            </div>
            <div className="bottom-button">
                <IonButton onClick={() => returnToLogin()} fill="clear">
                    Return to Login
                </IonButton>
            </div>
        </div>
        <IonLoading
          isOpen={showLoading}
          onDidDismiss={() => setShowLoading(false)}
          message={'Please wait...'}
          duration={5000}
        />
        <IonToast
          isOpen={showToastMessage}
          onDidDismiss={() => setShowToast(false)}
          message= {toastMessage}
          duration={5000}
        />
      </IonContent>
    </IonPage>
  );
};

export default ForgotPassword;
