import React, { useState } from 'react';
import { IonContent, IonPage, IonList, IonItem, IonLabel, IonImg, IonItemDivider } from '@ionic/react';
import { Plugins } from '@capacitor/core';
const { Storage } = Plugins;

const Contacts: React.FC = (props) => {
  function goForward(value: any){
    (props as any).history.push({pathname: '/contact', state: {contact: value}});
  }

  const [contacts, setContacts] = useState([] as any);
  const [contactKeys, setContactKeys] = useState([] as string[]);
  const [user, setUser] = useState({id: '', name: '', email: '', token: ''});

  async function loadContacts() {
    const { value } = await Storage.get({ key: 'user' });
    let data: any;

    if(value){
        data = JSON.parse(value);
        setUser(data);

        var jwt = require('jwt-decode');
        let tokenDecode = jwt(data.token);

        if(tokenDecode.exp < Math.floor(Date.now() / 1000)){
          await Storage.remove({ key: 'user' });
          (props as any).history.push('/login');
        }
    } else {
      (props as any).history.push('/login');
    }

    let url = 'api/v1/contacts';
    let response = await fetch(
      url,{
      method: 'GET',
      headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization' : 'Bearer ' + data.token
        }
      }
    );
          
    data = await response.json();

    if(data.error)
    {
      console.log(data.error);
    }
    else
    {
      data.sort((a:any, b:any) => (a.industry > b.industry) ? 1 : -1);
      
      let _ = require('lodash');
      let groupedContacts = _.mapValues(_.groupBy(data, 'industry'));
      let contactKeys : string[] = [];
      contactKeys = Object.keys(groupedContacts);
      
      setContacts(groupedContacts);
      setContactKeys(contactKeys);
    }
  }

  if(!user.name){
    loadContacts();
  }

  function processItems(keys:string[], itemArray: any) {
    // Create an empty array that will hold the final JSX output.
    let buffer:any = [];

    if(itemArray) { 
      keys.forEach( (value, key) => {
        buffer.push(<IonItemDivider key={key}><IonLabel>{value}</IonLabel></IonItemDivider>)
        itemArray[value].forEach( (childValue: any, childKey: any) => {
          buffer.push(<IonItem key="{key}" button onClick={() => {goForward(childValue)}} class="contact-item">
          <div>
            <div>
              <IonImg class="contact-image" src={childValue.avatar} />
            </div>
            <div>
              <IonLabel class="contact-label ion-text-wrap">
                <h4>{childValue.name}</h4>
                <p>{childValue.industry}</p>
                <p>{childValue.state}</p>
              </IonLabel>
            </div>
          </div>
        </IonItem>)
        });
      });


      // And return the buffer for display inside the render() function
      return (
          <div>
            {buffer}
          </div>
      );
    }
  }

  return (
    <IonPage>
      <IonContent className="contact-list">
          <div className="contact-list-spacer"></div>
          <IonList>
          {
            processItems(contactKeys, contacts)
          }
          </IonList>
      </IonContent>
    </IonPage>
  );
};

export default Contacts;
